import React from 'react';
import './pageStyle.scss';
import styled from 'styled-components';
import { CSSTransitionGroup } from 'react-transition-group';

const Wrapper = styled.div`
    .fade-appear {
        opacity: 0.01;
    }

    .fade-appear.fade-appear-active {
        opacity: 1;
        transition: opacity 1s ease-in;
    }

    .fadeName-appear {
        opacity: 0.01;
    }

    .fadeName-appear.fadeName-appear-active {
        opacity: 1;
        transition: opacity 1.5s ease-in;
        transition-delay: 1s;
    }

    .nameHeader {
        margin-bottom: 10vh;
    }
`;

const About = styled.div`
    #subText {
        margin-top: 10vh;
    }

    a {
        color: #b9f442;
    }

    .fadeText-appear {
        opacity: 0.01;
    }

    .fadeText-appear.fadeText-appear-active {
        opacity: 1;
        transition: opacity 1s;
        transition-delay: 2.75s;
    }
`;

class AboutPage extends React.Component {
    render() {
        return (
            <div className='patterned-page' id='about-page'>
                <div className='rowVertical'>
                    <Wrapper>
                    <CSSTransitionGroup
                        key ='hello'
                        transitionName = 'fade'
                        transitionAppear = {true}
                        transitionAppearTimeout = {2500}                   
                    >
                    <h1 className='nameHeader' key='hello'>
                        Hello 
                        <CSSTransitionGroup
                            key='name'
                            transitionName = 'fadeName'
                            transitionAppear = {true}
                            transitionAppearTimeout = {6000}>
                        <span key='name'>, I'm Daylan. </span>
                        </CSSTransitionGroup>
                    </h1>
                    </CSSTransitionGroup>
                    </Wrapper>
                </div>
                <About>
                <CSSTransitionGroup
                        key='sub'
                        transitionName='fadeText'
                        transitionAppear={true}
                        transitionAppearTimeout={0}>
                <div className='rowVertical' id='subText' key='sub'>
                    <p>I'm a software developer located in Edmonton, AB</p>
                    <p>I'm passionate about all things tech, (especially data sciences)</p>
                    <p> 
                        <a href="mailto:daylanvig@gmail.com?Subject=Website%20Contact" target='_top'>Contact me </a>
                        to learn more
                    </p>
                </div>
                </CSSTransitionGroup>
                </About>
                
            </div>

        );
    }
}

export default AboutPage;