import React from 'react';
import ScrollIntoView from 'react-scroll-into-view';
import './style.scss';

import {
    Collapse,
    Navbar,
    NavbarToggler,
    NavbarBrand,
    Nav,
    NavItem,
    Button,
    NavLink} from 'reactstrap';


class NavBar extends React.Component {

    constructor(props){
        super(props);
        this.toggle = this.toggle.bind(this);
        this.state = {
          isOpen: false,
          display: 'none'
        };
    }
    toggle() {
        this.setState({
            isOpen: !this.state.isOpen
          });
    }

    componentDidMount() {
        window.addEventListener('scroll', () => this.scroll());
    }
    
    scroll() {
        if(window.pageYOffset > 10) {
            this.setState({
                display: 'flex'
            });
        }
        if(window.pageYOffset === 0){
            this.setState({
                display: 'none'
            });
        }
    }

    render() {
        const divStyle = {
            style: {
                display: this.state.display
            }
        };
        const {style} = divStyle;

        return (
            <div>
                <Navbar id='navbar' dark expand='sm' fixed='top' style={style}>
                    <NavbarBrand id='navBrand' href='/'>DV</NavbarBrand>
                    <NavbarToggler onClick={this.toggle} />
                    <Collapse isOpen={this.state.isOpen} navbar>
                    <Nav className="ml-auto" navbar>
                    <NavItem>
                        <ScrollIntoView selector='#about-page'>
                            <Button color='link' style={{color: 'white'}}>About</Button>
                        </ScrollIntoView>
                    </NavItem>
                    <NavItem>
                        <ScrollIntoView selector='#skills-page'>
                        <Button color='link' style={{color: 'white'}}>Skills</Button>
                        </ScrollIntoView>
                    </NavItem>
                    <NavItem>
                        <ScrollIntoView selector='#projects-page'>
                        <Button color='link' style={{color: 'white'}}>Portfolio</Button>
                        </ScrollIntoView>
                    </NavItem>
                    <NavItem>
                        <ScrollIntoView selector='#experience-page'>
                        <Button color='link' style={{color: 'white'}}>Experience</Button>
                        </ScrollIntoView>
                    </NavItem>
                    <NavItem>
                        <NavLink >
                        <a href="mailto:daylanvig@gmail.com?Subject=Website%20Contact" target='_top' style={{color: '#b9f442'}}>Contact me </a>
                        </NavLink>
                    </NavItem>
            </Nav>
            </Collapse>
            </Navbar>

            </div>

        );

    }
}

export default NavBar;