import React from 'react';
import './pageStyle.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faJava,
            faJsSquare,
            faPython,
            faHtml5,
            faCss3,
            faSass,
            faGithub} from '@fortawesome/free-brands-svg-icons';


class SkillsPage extends React.Component {

    render() {
        return (
            <div className='page' id='skills-page'>
            <div className='row'>
                <h1 className='header'>Major Skills</h1>
                <div className='skillCol'>
                    <FontAwesomeIcon icon={faJava} size='8x'/>
                    <h3>Java</h3>
                </div>    
                <div className='skillCol'>
                    <img src={require('../static/dbicon.png')} />
                    <h3>MySQL</h3></div>   
                <div className='skillCol'>
                    <FontAwesomeIcon icon={faPython} size='8x'/>
                    <h3>Python</h3>
                    <p>Experience with a variety of libraries including Flask, SQLAlchemy, NLTK</p>
                </div>      
                <div className='skillCol'>
                    <FontAwesomeIcon icon={faJsSquare} size='8x'/>
                    <h3>JavaScript</h3>
                    <p>Experience working with React, Bootstrap, jQuery, Quill</p>
                </div>   
                <div className='skillCol'>
                    <FontAwesomeIcon icon={faHtml5} size='8x'/>
                    <h3>HTML5</h3>
                </div>
                <div className='skillCol'>
                    <FontAwesomeIcon icon={faCss3} size='8x'/>
                    <h3>CSS</h3>
                </div>
                </div>
                <div className='row'>
                <h1 className='header'>Familiar With</h1>
                <div className='skillCol'>
                    <FontAwesomeIcon icon={faSass} size='8x'/>
                    <h3>SCSS</h3>
                </div>    
                <div className='skillCol'>
                    <img src={require('../static/cppicon.png')} />
                    <h3>C++</h3>
                </div>   
                <div className='skillCol'>
                    <img src={require('../static/aspicon.png')} />
                    <h3>ASP.NET</h3>
                </div>
                <div className='fillerCol'></div>        
                <div className='skillCol'>
                    <FontAwesomeIcon icon={faGithub} size='8x'/>
                    <h3>Git</h3></div>   
                <div className='fillerCol2'></div> 
                <div className='skillCol'>
                    <img src={require('../static/mvcicon.png')} />
                    <h3>MVC Architecture and Design Patterns</h3>
                </div>
            </div>            
            </div>

        );
    }
}

export default SkillsPage;