import React from 'react';
import './pageStyle.scss';
import styled from 'styled-components';


const Timeline = styled.div`
    width: 50%;
    margin: 0 auto;
    display: flex;
    h4 {
        font-size: 10pt;
    }

    .time {
        width: 95%;
        float: right;
        flex-wrap: wrap;
        text-align: right;
    }
    
    .timeRight {
        width: 95%;
        text-align: left;
    }

    ul {
        list-style: none;
        margin: 0;
        border: none;
    }

    li {
        width: 100%;
        margin: 0px;
        padding: 0px;
        border: none;
        display: block;
    }
`;

class ExperiencePage extends React.Component {
    render() {
        return (
            <div className='page' id='experience-page'>
                <div className='row'>
                    <h1 className='header'>Experience &amp; Education</h1>
                    <Timeline>
                        <div className='time'><h4>2016-2018</h4>
                        <h4>Athabasca University</h4>
                        <h4>B.Sc. in Computing and Information Systems - GPA 3.5</h4>
                        </div>
                    </Timeline>
                    <Timeline></Timeline>
                    <Timeline></Timeline>
                    <Timeline>
                        <div className='timeRight'>
                            
                            <ul>
                            <h4>2015-2017</h4>
                            <h4>Financial Services Representative</h4>
                                <li>
                                    <p className='text-muted'>Consistently demonstrated top quartile sales performance by effectively managing a client pipeline to ensure application deadlines were met and future business opportunities pursued.</p>
                                </li>
                                <li>
                                    <p className='text-muted'>Proactively assisted with other areas of the branch to deepen business relationships and increase branch wide customer service performance levels, while simultaneously identifying additional opportunities. </p>
                                </li>
                                <li>
                                    <p className='text-muted'>Supported the branch team by assuming additional coaching responsibilities to assist underperforming Customer Service Representatives reach their individual sales targets. Quarter-over-quarter performance increases ranged from 15% - 35%.</p>
                                </li>
                            </ul>
                        </div>
                    </Timeline>
                    <Timeline>
                        <div className='time'><h4>2014-2015</h4>
                        <h4>Customer Service Representative</h4>
                        <ul>
                            <li>
                                <p className='text-muted'>Consistently exceeded regional sales targets, operating at 250% – 500% of target pacing</p>
                            </li>
                            <li>
                                <p className='text-muted'>Acted as branch customer service lead, responsible for introducing new initiatives to improve branch wide customer service scores and support the organization of events designed to recognize staff members for exceptional performance.</p>
                            </li>
                            <li>
                                <p className='text-muted'>Worked with wealth advisors to identify high value prospects for additional business opportunities. Communicated these opportunities with clients to show them the value of deepening their business relationships.</p>
                            </li>
                        </ul>
                        </div>
                    </Timeline>
                    <Timeline></Timeline>
                </div>
            </div>
        );
    }
}

export default ExperiencePage;