import React from 'react';
import './pageStyle.scss';
import styled from 'styled-components';

const Row = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    align-content: space-around;
    text-align: center;
    flex-wrap: wrap;
    .header {
        width: 100%;
        margin-top: 15px;
    }

    .projCol {
        padding: 20px;
        width: 100%;
        margin: 0 auto;
        @media screen and (min-width: 750px) {
            width: 33%;
        }
    }

    p {
        margin-top: 5pt;
    }
    .projTitle {
        font-size: 12pt;
        margin-top: 10pt;
        margin-bottom: 5pt;
    }
`;

class ProjectsPage extends React.Component {

    render() {
        return (
            <div id='projects-page'>
            
                <Row>
                    <div><h1 className='header'>Projects</h1></div>
                </Row>
                <Row>
                <h2 className='header'>On-The-Go</h2>
                </Row>
                <Row>
                    <div className='projCol'>
                        <div className='background'>
                            <img src={require('../static/nlp.jpeg')} />
                            <p className='projTitle'>Python Based Neural Network - Natural Language Processing</p>
                        </div>
                    </div>
                    <div className='projCol'>
                        <div className='background'>
                            <img src={require('../static/calendar.jpg')} />
                            <p className='projTitle'>ASP.NET Scheduling Application</p>
                        </div>                    
                    </div>
                </Row>
                <Row>
                    <h2 className='header'>Completed</h2>
                </Row>
                <Row><div className='projCol'>
                        <div className='background'>
                            <img src={require('../static/greenhouse.jpg')} />
                            <p className='projTitle'>Java Based Greenhouse Controller</p>
                            <p>Designed to control the mechnical aspects of a greenhouse such as water, fans, lighting. 
                                Included functionality for error logging and recovery.
                            </p>
                        </div>
                </div>
                <div className='projCol'>
                        <div className='background'>
                            <img src={require('../static/phone.jpg')} />
                            <p className='projTitle'>Java Based Point of Sale User Interface</p>
                            <p>Designed an interface for a mobile point of sale system.</p>
                        </div>
                </div>
                <div className='projCol'>
                        <div className='background'>
                            <img src={require('../static/j3d.png')} />
                            <p className='projTitle'>Java3D Animation and Game Design</p>
                            <p>Developed various player controllable games, as well as physical models</p>
                        </div>
                </div>
                </Row>
                </div>
                
            

        );
    }
}

export default ProjectsPage;